import { advancedFormat, dayjs, timezone, utc } from '../legacy-libs'

dayjs.extend(utc)
dayjs.extend(timezone)
dayjs.extend(advancedFormat)

export function getTimeExpire(min: number) {
  const fmt = dayjs().tz('Asia/Shanghai').add(min, 'minute').format('YYYY-MM-DDTHH:mm:ss+z')
  const ret = fmt.match(/.*(GMT\+(\d))/)
  if (ret == null) {
    return ''
  } else {
    return ret[0].replace(ret[1], ret[2].padStart(2, '0') + ':00')
  }
}

export function getTimeByDay(day: number): Date {
  return dayjs().tz('Asia/Shanghai').add(day, 'day').toDate()
}
