import { inject, injectable } from 'inversify'
import { LoggerService } from '@nestjs/common'
import { PrismaClientSymbol } from '@flowda-projects/flowda-types'
import { CustomerUpdateAmountDto } from './dto/customerUpdateAmount.dto'
import * as db from '@prisma/client-flowda'
import { CustomerService } from './customer.service'

@injectable()
export class CustomerTx {
  private readonly logger: LoggerService

  constructor(
    @inject(CustomerService) private service: CustomerService,
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(CustomerTx.name)
  }

  async amountUpdate(
    option: CustomerUpdateAmountDto & { userInfo: (db.Customer & { profile: any }) | null },
  ): Promise<any> {
    return this.prisma.$transaction(tx => this.service.amountUpdate(option, { tx }))
  }
}
