import * as nodemailer from 'nodemailer'
import { inject, injectable } from 'inversify'
import { Logger } from '@nestjs/common'
import { IMailOptions, IMailService, TSendEmailOptions } from '../interfaces/mail/mail.service'
import { IConfigService } from '../interfaces/config/config.service'
import { render } from './templates'

@injectable()
export class MailService implements IMailService {
  private readonly logger = new Logger(MailService.name)

  constructor(@inject(IConfigService) private config: IConfigService) {}

  private getTransporter = () => {
    return nodemailer.createTransport({
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore host 类型在 SMTPTransport 不存在？
      host: this.config.getEnv('mail_smtp'),
      port: this.config.getEnv('mail_smtp_port') as unknown as number,
      secure: true,
      auth: {
        user: this.config.getEnv('mail_account'),
        pass: this.config.getEnv('mail_secret'),
      },
    })
  }

  private async _sendMail(options: IMailOptions) {
    const transporter = this.getTransporter()
    try {
      // SMTP 发信限制规则
      // 发信频率（调用 SMTP 服务的整体频率）200 封/100 秒
      // 单日发信上限（一个发件人单日发信上限） 450 封
      await transporter.sendMail(options)
    } catch (error) {
      this.logger.error('send mail exception: ', error)
      // TODO: Emergency Alert
    }
  }

  async sendLicense(email: string, license: string): Promise<void> {
    this.logger.log(`sending license ${email}`)
    const { html, subject } = render('LICENSE', { license })

    await this._sendMail({
      from: this.config.getEnv('mail_account'),
      to: email,
      subject,
      html,
    })

    // const transporter = this.getTransporter();
    // await transporter.sendMail({
    //   to: email,
    //   from: '"Support Team" <support@webinfra.cloud>',
    //   subject: 'Thank you for buying the nice product!',
    //   text: ' Your license is\n' + license,
    // })
  }

  async sendEmail({ email, templateName, params }: TSendEmailOptions): Promise<void> {
    this.logger.log(`sending email ${email}`)
    const { html, subject } = render(templateName, params)

    await this._sendMail({
      from: this.config.getEnv('mail_account'),
      to: email,
      subject,
      html,
    })
  }

  async legacySendEmail(email: string, subject: string, content: string): Promise<void> {
    this.logger.log(`sending email ${email}`)
    const transporter = nodemailer.createTransport({
      host: 'smtp.feishu.cn',
      port: 465,
      secure: true,
      auth: {
        user: this.config.getEnv('mail_account'),
        pass: this.config.getEnv('mail_secret'),
      },
    })

    await transporter.sendMail({
      to: email,
      from: '"Support Team" <qa@webinfra.cloud>',
      subject: subject,
      text: content,
    })
  }
}
