import { inject, injectable, named, postConstruct } from 'inversify'
import * as bcrypt from 'bcrypt'
import {
  AuthenticationError,
  IdentityProviderServiceSymbol,
  IIdentity,
  IIdentityProviderService,
  PrismaClientSymbol,
  SdkError,
  WXError,
} from '@flowda-projects/flowda-types'
import { JwtService } from '../jwt/jwt.service'
import { IConfigService, IMailService } from '../../infra'
import { Customer, PrismaClient } from '@prisma/client-flowda'
import {
  AuthenticationService,
  excludedIdentity,
  excludedIdentityAndRefreshToken,
  TExcludedIdentity,
  TExcludedIdentityAndRefreshToken,
} from '../authentication/authentication.service'
import { customerPreSignupDto } from './dto/customerPreSignup.dto'
import { CustomerSignupDto } from './dto/customerSignup.dto'
import { CustomerEmailSignupDto } from './dto/customerEmailSignup.dto'
import { customerPreSignupResDto } from './dto/customerPreSignupRes.dto'
import { WxLoginService } from '../wx-login/wxLogin.service'
import { WxFwhLoginService } from '../wx-login/wxFwhLogin.service'

@injectable()
export class CustomerAuthService extends AuthenticationService<Customer> {
  constructor(
    @inject(IdentityProviderServiceSymbol)
    @named('customer')
    protected identityProvider: IIdentityProviderService<Customer>,
    @inject(JwtService) protected jwt: JwtService,
    @inject(IConfigService) protected config: IConfigService,
    @inject(IMailService) protected mailService: IMailService,
    @inject(PrismaClientSymbol) private prisma: PrismaClient,
    @inject(WxLoginService) private wxLogin: WxLoginService,
    @inject(WxFwhLoginService) private wxFwhLogin: WxFwhLoginService,
  ) {
    super(identityProvider, jwt, config, mailService)
  }

  @postConstruct()
  postConstruct() {
    this.setOptions({
      access_token_secret: this.config.getEnv('customer_access_token_secret'),
      refresh_token_secret: this.config.getEnv('customer_refresh_token_secret'),
      access_token_expire: this.config.getEnv('customer_access_token_expire'),
      refresh_token_expire: this.config.getEnv('customer_refresh_token_expire'),
    })
  }

  /**
   * 预注册
   * sdk.register
   */
  async preSignup(dto: customerPreSignupDto): Promise<customerPreSignupResDto> {
    // 一个邮箱只能注册一个
    const user = await this.identityProvider.find(dto)
    if (user) {
      throw new AuthenticationError.EmailAlreadyExists()
    }
    const app = await this.prisma.app.findUniqueOrThrow({ where: { id: dto.appId } })

    const randomCode = this.generateRandomUppercaseLetter()

    await this.prisma.customerPreSignup.create({
      data: {
        tenantId: app.tenantId,
        appId: dto.appId,
        email: dto.email,
        verifyCode: randomCode,
      },
    })

    await this.mailService.legacySendEmail(
      dto.email,
      'sdk customer register verify code',
      randomCode,
    )

    return {
      verifyCode: randomCode,
    }
  }

  async signup(
    dto: CustomerSignupDto,
    extraFields: Record<string, any> = {},
  ): Promise<TExcludedIdentity<Customer>> {
    const user = await this.identityProvider.find({
      appId: dto.appId,
      name: dto.name,
    })
    if (user) {
      throw new AuthenticationError.AccountNameAlreadyExists()
    }

    let hashedPassword: string | null = null
    if (dto.password) {
      // todo: 先不做验证这一步，邮箱需要，但是微信/手机号不需要
      hashedPassword = await bcrypt.hash(dto.password, 10)
    }

    // const { password, ...rest } = dto // 删除 password
    const app = await this.prisma.app.findUniqueOrThrow({ where: { id: dto.appId } })

    const a = {
      ...dto, // 所有属性（除 password），identity 和非 identity 属性透传，这样子类可以按需保存
      tenantId: app.tenantId,
      hashedPassword: hashedPassword,
      hashedRefreshToken: null,
      recoveryCode: null,
      recoveryToken: null,
      ...extraFields,
    }

    const newUser = await this.identityProvider.create(a)

    return excludedIdentity<Customer>(newUser)
  }

  async verifyAndSignup(dto: CustomerEmailSignupDto): Promise<TExcludedIdentity<Customer>> {
    const rt = await this.prisma.customerPreSignup.findFirst({
      where: {
        AND: [{ appId: dto.appId }, { email: dto.email }, { verifyCode: dto.verifyCode }],
      },
    })
    if (!rt) {
      throw new SdkError.WrongVerifyCode()
    }

    const newUser = await this.signup({
      appId: dto.appId,
      name: dto.name,
      password: dto.password,
      email: dto.email,
    })

    await this.prisma.customerPreSignup.delete({
      where: {
        id: rt.id,
      },
    })

    return newUser
  }

  async validateUser(
    nameField: 'email' | 'name',
    appId: string,
    name: string,
    password?: string,
  ): Promise<TExcludedIdentityAndRefreshToken> {
    const user = await this.doValidateUserWithApp(nameField, appId, name, password)
    return excludedIdentityAndRefreshToken(user)
  }

  async validateUserReturnTokens(
    nameField: string,
    appId: string,
    name: string,
    password?: string,
  ): Promise<{
    at: string
    rt: string
    user: TExcludedIdentityAndRefreshToken
    expireAt: number
  }> {
    const user = await this.doValidateUserWithApp(nameField, appId, name, password)
    const { at, exp } = this.jwt.generateAccessToken(user.id, {
      secret: this.options.access_token_secret,
      exp: this.options.access_token_expire,
    })
    return {
      at,
      rt: user.refreshToken,
      user: excludedIdentityAndRefreshToken(user),
      expireAt: exp,
    }
  }

  async wxValidateUser(
    code: string,
    appId: string,
  ): Promise<{
    at: string
    rt: string
    user: TExcludedIdentityAndRefreshToken
    expireAt: number
  }> {
    // appId valite
    if (!appId) {
      throw new AuthenticationError.InvalidAppId()
    }

    const ret = await this.wxLogin.getAccessToken(code)
    const data = ret.data

    const findCustomerRet = await this.identityProvider.find({ name: data.unionid, appId })

    let customer
    if (!findCustomerRet) {
      // 如果不存在则创建
      // 微信注册，只需要 name
      customer = await this.signup({
        appId,
        name: data.unionid,
      })
      const wxUser = await this.wxLogin.getUser(data.openid, data.access_token)
      const app = await this.prisma.app.findUniqueOrThrow({ where: { id: appId } })
      // 创建微信信息
      await this.prisma.weixinProfile.create({
        data: {
          tenantId: app.tenantId,
          unionid: data.unionid,
          loginOpenid: data.openid,
          headimgurl: wxUser.headimgurl,
          nickname: wxUser.nickname,
          sex: wxUser.sex,
          customerId: customer.id,
        },
      })
    } else {
      customer = findCustomerRet
    }

    return this.validateUserReturnTokens('name', appId, customer.name)
  }

  // 匿名登录 <- name: 匿名token
  // 快捷支付 <- name: openid
  //    1. customer
  //    2. 一笔订单（只考虑了一笔）
  // 以上发生在 merge 之前
  async fwhLoginMerge(code: string, appId: string) {
    const ret = await this.wxFwhLogin.getAccessTokenByCode(code)
    // 拿到 unionid 和 openid
    // 根据 openid 找到匿名用户
    // 1. 根据 openid 拿 customer 1
    const customerByFwhOpenId = await this.prisma.customer.findFirst({
      where: {
        appId,
        name: ret.openid, // 注意在 queryPayQuick 的时候，已经将 name 改成了 openid
      },
    })
    if (!customerByFwhOpenId) {
      // 1. 没有支付 2. 已经合并(unionid)
      throw new WXError.RecoveryNoOrderFound()
    }

    // 2. 再尝试通过 unionid 去拿 customer 2
    const customerByUnionId = await this.identityProvider.find({ name: ret.unionid, appId })

    // 3. customer 1 \2 合并，customer+  order关联
    if (!customerByUnionId) {
      // 如果不存在，则 name 替换为 unionid
      // TODO: 应该添加 appId 限制
      customerByFwhOpenId.name = ret.unionid
      await this.prisma.customer.update({
        where: {
          id: customerByFwhOpenId.id,
        },
        data: customerByFwhOpenId,
      })
    } else {
      // 如果存在，则替换 order 的 customerId， todo 并且废弃掉之前的 customerByFwhOpenId
      await this.prisma.order.updateMany({
        where: {
          customerId: customerByFwhOpenId.id,
        },
        data: {
          customerId: customerByUnionId.id,
        },
      })
    }
    const customer = await this.validateUserReturnTokens('name', appId, ret.unionid)

    return customer
  }

  async fwhLogin(code: string, appId: string) {
    const ret = await this.wxFwhLogin.getAccessTokenByCode(code)
    const name = ret.unionid || ret.openid // 如果 unionid 为空，则 fallback 到 openid

    const findCustomerRet = await this.identityProvider.find({ name: name, appId })

    let customer
    if (!findCustomerRet) {
      // 如果不存在则创建
      // 微信注册，只需要 name
      customer = await this.signup({ name, appId })
    } else {
      customer = findCustomerRet
    }

    // 更新微信信息
    const wxUser = await this.wxLogin.getUser(ret.openid, ret.access_token)
    const app = await this.prisma.app.findUniqueOrThrow({ where: { id: appId } })

    await this.prisma.weixinProfile.upsert({
      where: {
        customerId: customer.id,
      },
      create: {
        tenantId: app.tenantId,
        unionid: ret.unionid,
        loginOpenid: ret.openid,
        headimgurl: wxUser.headimgurl,
        nickname: wxUser.nickname,
        sex: wxUser.sex,
        customerId: customer.id,
      },
      update: {
        unionid: ret.unionid,
        loginOpenid: ret.openid,
        headimgurl: wxUser.headimgurl,
        nickname: wxUser.nickname,
        sex: wxUser.sex,
      },
    })

    return this.validateUserReturnTokens('name', appId, customer.name)
  }

  // 服务号的state校验
  // 可能也要根据app做配置，每个app默认生成一个唯一的secret防止线上用户数据混淆
  async validateState(state: string) {
    const s = this.config.getEnv('fuwuhao_state_secret')
    return s === state
  }

  private async doValidateUserWithApp(
    nameField: string,
    appId: string,
    name: string,
    password?: string,
  ): Promise<IIdentity & { refreshToken: string }> {
    if (!appId) {
      throw new AuthenticationError.InvalidAppId()
    }
    const user = await this.identityProvider.find({
      [nameField]: name,
      appId,
    })
    if (!user) {
      throw new AuthenticationError.UserNamePasswordMismatch()
    }

    // 微信登录可以不用校验密码
    if (password != null) {
      if (!user.hashedPassword) {
        throw new AuthenticationError.NotInitPassword()
      }

      const match = await bcrypt.compare(password, user.hashedPassword)
      if (!match) {
        throw new AuthenticationError.UserNamePasswordMismatch()
      }
    }

    const { rt } = this.jwt.generateRefreshToken(user.id, {
      secret: this.options.refresh_token_secret,
      exp: this.options.refresh_token_expire,
    })
    const hash = await bcrypt.hash(rt, 10)
    user.hashedRefreshToken = hash
    const updatedUser = await this.identityProvider.update(user)
    // db 不要明文存 refresh token，但是 refresh token 要返回给 client
    return {
      ...user,
      refreshToken: rt,
    }
  }
}
