export const IdentityProviderServiceSymbol = Symbol.for('IIdentityProviderService')

export interface IIdentity {
  id: string
  name: string
  appId?: string
  hashedPassword: string | null
  hashedRefreshToken: string | null
  recoveryCode: string | null
  recoveryToken: string | null
}

export interface IIdentityProviderService<T extends IIdentity> {
  // Partial<T> 不行
  find(query: any): Promise<T | null>

  update(user: IIdentity): Promise<T>

  create(user: Omit<IIdentity, 'id'>): Promise<T>
}
