import { inject, injectable } from 'inversify'
import { PrismaClientSymbol } from '@flowda-projects/flowda-types'
import * as db from '@prisma/client-wms'
import axios from 'axios'
import * as _ from 'lodash'

const REST_URL = 'http://localhost:8070/engine-rest'

@injectable()
export class TaskService {
  constructor(@inject(PrismaClientSymbol) private prisma: db.PrismaClient) {}

  async get(taskId: string) {
    const res = await axios.get(REST_URL + `/task/${taskId}`)
    const taskDefinitionKey = res.data.taskDefinitionKey

    const rel = await this.prisma.resourceTaskDefKeyRelation.findUnique({
      where: {
        taskDefKey: taskDefinitionKey,
      },
    })
    if (rel) {
      return {
        taskId: taskId,
        taskDefKey: taskDefinitionKey,
        resource: rel.resource,
      }
    }
    return undefined
  }

  async complete(taskId: string, body: any) {
    const { resource, data } = body
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    const ret1 = await this.prisma[_.lowerFirst(resource)].create({
      data: data,
    })
    try {
      const ret2 = await axios.post(
        REST_URL + `/task/${taskId}/complete`,
        {},
        {
          headers: {
            'Content-Type': 'application/json',
            Accept: 'application/json',
          },
        },
      )
    } catch (e) {
      console.error(e)
    }

    return {
      taskId,
      resource,
      data,
      ret1,
    }
  }
}
