import { inject, injectable } from 'inversify'
import { LoggerService } from '@nestjs/common'
import { PrismaClientSymbol } from '@flowda-projects/flowda-types'
import { excludedIdentity, TExcludedIdentity } from '../authentication/authentication.service'
import * as db from '@prisma/client-flowda'

type QueryResDto = Array<
  db.Prisma.OrderGetPayload<{
    include: {
      productSnapshots: true
    }
  }> & {
    customer: TExcludedIdentity<db.Customer>
  }
>

@injectable()
export class OrderQuery {
  private readonly logger: LoggerService

  constructor(
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject('Factory<Logger>')
    loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(OrderQuery.name)
  }

  async query(orderId: string): Promise<QueryResDto> {
    const ret = await this.prisma.order.findMany({
      where: { id: orderId },
      include: {
        customer: true,
        productSnapshots: true,
      },
    })
    return ret.map(item => ({
      ...item,
      customer: excludedIdentity(item.customer),
    }))
  }

  async findAll(query?: any): Promise<any> {
    console.log('find all order: ', query)
    let limit = 100
    if (query?.limit) {
      limit = query.limit
      delete query.limit
    }
    const ret = await this.prisma.order.findMany({
      where: query || {},
      take: limit,
      include: {
        customer: true,
        productSnapshots: false,
      },
    })
    return ret
  }

  async count(query?: any) {
    if (query?.limit) {
      delete query.limit
    }
    return this.prisma.order.count({
      where: query,
    })
  }

  async queryOrderHistory(customerId: string, productId?: string) {
    const orders = await this.prisma.order.findMany({
      where: {
        customerId,
      },
      include: {
        productSnapshots: true,
      },
    })
    if (!productId) {
      return orders
    }
    return orders
      .map(orderData => {
        const pid = orderData.productSnapshots[0].productId
        if (pid === productId) {
          return orderData
        }
        return null
      })
      .filter(item => !!item)
  }
}
