import { inject, injectable } from 'inversify'
import * as jwt from 'jsonwebtoken'
import { JwtPayload } from 'jsonwebtoken'
import { IConfigService } from '../../infra/index'

@injectable()
export class JwtService {
  constructor(@inject(IConfigService) private config: IConfigService) {}

  generateAccessToken(
    userId: string,
    options: { exp: number; secret: string },
  ): {
    at: string
    iat: number
    exp: number
  } {
    const payload = {
      uid: userId,
    }
    const token = jwt.sign(payload, options.secret, {
      expiresIn: `${options.exp}s`,
    })

    const decode = jwt.decode(token) as JwtPayload
    return {
      at: token,
      iat: decode.iat as number,
      exp: decode.exp as number,
    }
  }

  generateRefreshToken(
    userId: string,
    options: { exp: number; secret: string },
  ): {
    rt: string
    iat: number
    exp: number
  } {
    const payload = {
      uid: userId,
    }
    const token = jwt.sign(payload, options.secret, {
      expiresIn: `${options.exp}s`,
    })
    const decode = jwt.decode(token) as JwtPayload
    return {
      rt: token,
      iat: decode.iat as number,
      exp: decode.exp as number,
    }
  }

  verifyRefreshToken(rt: string, options: { secret: string }): JwtPayload {
    return jwt.verify(rt, options.secret) as JwtPayload
  }

  generateRecoveryToken(email: string, options: { secret: string }): string {
    const recoveryToken = jwt.sign(
      {
        email: email,
        verificationToken: true,
        exp: Math.floor(Date.now() / 1000) + 30 * 60, // 30 分钟过期
      },
      options.secret,
    )
    return recoveryToken
  }

  verifyRecoveryToken(at: string, options: { secret: string }): JwtPayload {
    return jwt.verify(at, options.secret) as JwtPayload
  }
}
