import { inject, injectable } from 'inversify'
import { LoggerService } from '@nestjs/common'
import { PrismaClientSymbol } from '@flowda-projects/flowda-types'
import * as db from '@prisma/client-flowda'

// todo: 借着创建 super admin 账户，准备重构 authentication
@injectable()
export class SuperAdminAuthenticationQuery {
  private readonly logger: LoggerService

  constructor(
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject('Factory<Logger>')
    loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(SuperAdminAuthenticationQuery.name)
  }

  mathRoles(roles: string[], tenant: db.Tenant): boolean {
    if (tenant.role == null) return false
    if (roles.indexOf('superadmin') > -1) return true
    if (roles.indexOf(tenant.role) > -1) return true
    return false
  }

  getTenantList() {
    return this.prisma.tenant.findMany()
  }
}
