import { injectable } from 'inversify'
import { AuthenticationError, PrismaTx } from '@flowda-projects/flowda-types'
import * as bcrypt from 'bcrypt'
import * as db from '@prisma/client-flowda'
import { SupperAdminSignupDto } from './dto/supperAdminSignup.dto'

// todo: 借着创建 super admin 账户，准备重构 authentication
@injectable()
export class SuperAdminAuthenticationService {
  // todo: 后续换成 zod
  async signup(dto: SupperAdminSignupDto, { tx }: PrismaTx): Promise<db.Tenant> {
    const tenant = await tx.tenant.findFirst({
      where: {
        name: dto.username,
      },
    })
    if (tenant) {
      throw new AuthenticationError.AccountNameAlreadyExists()
    }
    const hashedPassword = await bcrypt.hash(dto.password, 10)
    const newTenant = await tx.tenant.create({
      data: {
        name: dto.username,
        hashedPassword: hashedPassword,
        email: dto.email,
        role: 'superadmin',
      },
    })

    return newTenant
  }
}
