export const ProfileScalarFieldEnumSchema = z.enum(['id','createdAt','updatedAt','customerId','productType','plan','amount','expireAt','tenantId']);

export const QuestionScalarFieldEnumSchema = z.enum(['id','uid','question','answer','success','createdAt','updatedAt']);

export const SortOrderSchema = z.enum(['asc','desc']);

