// APP SCHEMA
/////////////////////////////////////////

export const AppSchema = z.object({
  id: z.string().cuid(),
  createdAt: z.date(),
  updatedAt: z.date(),
  name: z.string().openapi({"title":"应用id","access_type":"read_only"}),
  hashedAppToken: z.string().nullable(),
  hashedPassword: z.string(),
  hashedRefreshToken: z.string().nullable(),
  recoveryCode: z.string().nullable(),
  recoveryToken: z.string().nullable(),
  displayName: z.string().openapi({"title":"应用名"}),
  description: z.string().nullable().openapi({"title":"应用描述"}),
  isDeleted: z.boolean().nullable(),
  tenantId: z.string(),
}).openapi({"primary_key":"id","searchable_columns":"name,displayName,description","display_column":"displayName","display_name":"应用","display_primary_key":"true"})

export type App = z.infer<typeof AppSchema>

// APP RELATION SCHEMA
//------------------------------------------------------

export type AppRelations = {
  products: ProductWithRelations[];
  customers: CustomerWithRelations[];
  orders: OrderWithRelations[];
  tenant?: TenantWithRelations | null;
};

export type AppWithRelations = z.infer<typeof AppSchema> & AppRelations

export const AppWithRelationsSchema: z.ZodObject<any> = AppSchema.merge(z.object({
  products: z.lazy(() => ProductWithRelationsSchema).array().openapi({"model_name":"Product","foreign_key":"appId","primary_key":"id","title":"Products"}),
  customers: z.lazy(() => CustomerWithRelationsSchema).array().openapi({"model_name":"Customer","foreign_key":"appId","primary_key":"id","title":"Customers"}),
  orders: z.lazy(() => OrderWithRelationsSchema).array().openapi({"model_name":"Order","foreign_key":"appId","primary_key":"id","title":"Orders"}),
  tenant: z.lazy(() => TenantWithRelationsSchema).nullable(),
}))

