import { Customer, Order, ProductSnapshot, Profile } from '@prisma/client-flowda'
import { ApiProperty } from '@nestjs/swagger'
import { TExcludedIdentity } from '../../authentication/authentication.service'

export class SdkCreateOrderBaseResDto {
  @ApiProperty({
    description: '返回订单信息',
  })
  order: Order

  @ApiProperty({
    description: '返回消费者信息',
  })
  customer: TExcludedIdentity<Customer> & { profile?: Profile }

  @ApiProperty({
    description: '返回产品的快照信息',
  })
  productSnapshot: ProductSnapshot
}

export class SdkCreateOrderResDto extends SdkCreateOrderBaseResDto {
  @ApiProperty({
    description: '返回支付二维码链接',
  })
  codeUrl?: string
}
