import { inject, injectable } from 'inversify'
import { LoggerService } from '@nestjs/common'
import { IIdentity, PrismaClientSymbol } from '@flowda-projects/flowda-types'
import { SdkCreateOrderDto } from './dto/sdkCreateOrder.dto'
import { SdkCreateOrderResDto } from './dto/sdkCreateOrderRes.dto'
import { OrderService, TQueryPayRes } from './order.service'
import { SdkCreateQuickOrderDto } from './dto/sdkCreateQuickOrder.dto'
import { TExcludedIdentity } from '../authentication/authentication.service'
import * as db from '@prisma/client-flowda'
import { SdkCreateOrderInJSAPIDto } from './dto/sdkCreateOrderInJSAPI.dto'

@injectable()
export class OrderTx {
  private readonly logger: LoggerService

  constructor(
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject(OrderService) private service: OrderService,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(OrderTx.name)
  }

  async create(user: IIdentity, dto: SdkCreateOrderDto): Promise<SdkCreateOrderResDto> {
    return this.prisma.$transaction(async tx => this.service.create(user, dto, { tx }))
  }

  async createJSAPI(user: IIdentity, dto: SdkCreateOrderInJSAPIDto): Promise<SdkCreateOrderResDto> {
    return this.prisma.$transaction(async tx => this.service.createJSAPI(user, dto, { tx }))
  }

  async createQuick(appId: string, dto: SdkCreateQuickOrderDto): Promise<SdkCreateOrderResDto> {
    return this.prisma.$transaction(async tx => this.service.createQuick(appId, dto, { tx }))
  }

  async queryPay(
    userId: string,
    orderId: string,
  ): Promise<
    TQueryPayRes & {
      customer: TExcludedIdentity<db.Customer> & { profile?: db.Profile }
    }
  > {
    return this.prisma.$transaction(async tx => this.service.queryPay(userId, orderId, { tx }))
  }

  async queryPayQuick(anonymousCustomerToken: string, orderId: string) {
    return this.prisma.$transaction(async tx =>
      this.service.queryPayQuick(anonymousCustomerToken, orderId, { tx }),
    )
  }

  public async doQueryPay(orderId: string): Promise<TQueryPayRes> {
    return this.prisma.$transaction(async tx => this.service.doQueryPay(orderId, { tx }))
  }

  public async createOrder(customerId: string) {
    return this.prisma.$transaction(async tx => this.service.createOrder(customerId, { tx }))
  }
}
