import { inject, injectable } from 'inversify'
import { IIdentityProviderService, PrismaClientSymbol } from '@flowda-projects/flowda-types'
import { App, PrismaClient } from '@prisma/client-flowda'
import * as jwt from 'jsonwebtoken'
import { IConfigService } from '../../infra'

@injectable()
export class AppIdentityProviderService implements IIdentityProviderService<App> {
  constructor(
    @inject(PrismaClientSymbol) private prisma: PrismaClient,
    @inject(IConfigService) protected config: IConfigService,
  ) {}

  async create(dto: Omit<App, 'id'> & { password: string }): Promise<App> {
    const hashedAppToken = jwt.sign(
      {
        appId: dto.name,
        appToken: dto.password,
      },
      this.config.getEnv('app_token_secret'),
    )

    const user = await this.prisma.app.create({
      data: {
        tenantId: dto.tenantId,
        name: dto.name,
        hashedAppToken: hashedAppToken,
        hashedPassword: dto.hashedPassword,
        hashedRefreshToken: null,
        displayName: dto.displayName,
        description: dto.description,
      },
    })
    return user
  }

  find(query: Partial<App>): Promise<App | null> {
    const or: any[] = []
    if (query.id) {
      or.push({ id: query.id })
    }
    if (query.name) {
      or.push({ name: query.name })
    }
    return this.prisma.app.findFirst({
      where: {
        OR: or,
      },
    })
  }

  update(user: App): Promise<App> {
    return this.prisma.app.update({
      where: { id: user.id },
      data: user,
    })
  }
}
