import { inject, injectable } from 'inversify'
import { PrismaClientSymbol } from '@flowda-projects/flowda-types'
import { LoggerService } from '@nestjs/common'
import * as db from '@prisma/client-flowda'
import * as jwt from 'jsonwebtoken'
import { IConfigService } from '../../infra'
import { AppUpdateDto } from './dto/dto'
import { JwtPayload } from 'jsonwebtoken'

@injectable()
export class AppService {
  private readonly logger

  constructor(
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject(IConfigService) protected config: IConfigService,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(AppService.name)
  }

  async findAll() {
    const apps = await this.prisma.app.findMany({ where: { isDeleted: false } })
    return apps.map(app => ({
      ...app,
      appToken: this.decode(app.hashedAppToken),
    }))
  }

  findByTenantId(tenantId: string) {
    // todo: 租户id
    return this.prisma.app
      .findMany({
        where: {
          tenantId: tenantId,
        },
      })
      .then(list => {
        return list.map((item: any) => {
          if (item.hashedAppToken) {
            const token = this.decode(item.hashedAppToken)
            item.appToken = token
          }
          return item
        })
      })
  }

  async update(toApp: AppUpdateDto): Promise<db.App> {
    const app = await this.prisma.app.update({
      where: {
        id: toApp.id,
      },
      data: {
        displayName: toApp.displayName,
        description: toApp.description,
      },
    })
    return app
  }

  async findById(id: string): Promise<(db.App & { appToken: string }) | null> {
    const app = await this.prisma.app.findFirst({
      where: {
        id: id,
        isDeleted: false,
      },
    })
    if (app === null) {
      return null
    }

    const nextApp: any = app as db.App & { appToken: string }
    nextApp.appToken = this.decode(app.hashedAppToken)
    return nextApp
  }

  private decode(token: string | null): string | null {
    if (!token) {
      return null
    }
    const decode = jwt.verify(token, this.config.getEnv('app_token_secret')) as any
    return decode.appToken
  }
}
