import { inject, injectable } from 'inversify'
import { IConfigService } from '../../infra'
import { WechatOAuth, WechatOAuthFactorySymbol } from '../../legacy-libs'

export type TWxGetAccessTokenRes = {
  access_token: string
  expires_in: number
  refresh_token: string
  openid: string
  scope: string
  unionid: string
  create_at: number
}

export type TWxGetUser = {
  openid: string
  nickname: string
  sex: number
  headimgurl: string
  unionid: string
  language: string
  city: string
  province: string
  country: string
  privilege: any[]
}

@injectable()
export class WxLoginService {
  client: any

  constructor(
    @inject(IConfigService) private config: IConfigService,
    @inject(WechatOAuthFactorySymbol) private wechatOAuthFactory: () => typeof WechatOAuth,
  ) {}

  getAuthorizeURLForWebsite(redirectUrl: string) {
    const url = this.wechatOAuthFactory().getAuthorizeURLForWebsite(redirectUrl)
    return url
  }

  getAccessToken(code: string): Promise<{ data: TWxGetAccessTokenRes }> {
    return new Promise((resolve, reject) => {
      this.wechatOAuthFactory().getAccessToken(code, function (err: any, result: any) {
        if (err) {
          reject(err)
        } else {
          resolve(result)
        }
      })
    })
  }

  getUser(openid: string, access_token: string): Promise<TWxGetUser> {
    return new Promise((resolve, reject) => {
      this.wechatOAuthFactory()._getUser(
        {
          openid,
        },
        access_token,
        function (err: any, result: any) {
          if (err) {
            reject(err)
          } else {
            resolve(result)
          }
        },
      )
    })
  }
}
