import { z } from 'zod';
import type { Prisma } from '@prisma/client-wms';

/////////////////////////////////////////
// HELPER FUNCTIONS
/////////////////////////////////////////


/////////////////////////////////////////
// ENUMS
/////////////////////////////////////////

export const MemberInfoScalarFieldEnumSchema = z.enum(['id','createdAt','updatedAt','isDeleted','name','gender','idNo']);

export const ResourceTaskDefKeyRelationScalarFieldEnumSchema = z.enum(['id','createdAt','updatedAt','isDeleted','resource','taskDefKey']);

export const ResourceTaskIdRelationScalarFieldEnumSchema = z.enum(['id','createdAt','updatedAt','isDeleted','taskId','processInstanceId']);

export const SortOrderSchema = z.enum(['asc','desc']);

export const TransactionIsolationLevelSchema = z.enum(['ReadUncommitted','ReadCommitted','RepeatableRead','Serializable']);
/////////////////////////////////////////
// MODELS
/////////////////////////////////////////

/////////////////////////////////////////
// MEMBER INFO SCHEMA
/////////////////////////////////////////

export const MemberInfoSchema = z.object({
  id: z.number().int(),
  createdAt: z.date(),
  updatedAt: z.date(),
  isDeleted: z.boolean(),
  name: z.string(),
  gender: z.string(),
  idNo: z.string(),
}).openapi({"display_name":"成员信息"})

export type MemberInfo = z.infer<typeof MemberInfoSchema>

/////////////////////////////////////////
// RESOURCE TASK ID RELATION SCHEMA
/////////////////////////////////////////

export const ResourceTaskIdRelationSchema = z.object({
  id: z.number().int(),
  createdAt: z.date(),
  updatedAt: z.date(),
  isDeleted: z.boolean(),
  taskId: z.string(),
  processInstanceId: z.string(),
})

export type ResourceTaskIdRelation = z.infer<typeof ResourceTaskIdRelationSchema>

/////////////////////////////////////////
// RESOURCE TASK DEF KEY RELATION SCHEMA
/////////////////////////////////////////

export const ResourceTaskDefKeyRelationSchema = z.object({
  id: z.number().int(),
  createdAt: z.date(),
  updatedAt: z.date(),
  isDeleted: z.boolean(),
  resource: z.string(),
  taskDefKey: z.string(),
})

export type ResourceTaskDefKeyRelation = z.infer<typeof ResourceTaskDefKeyRelationSchema>
