import { inject, injectable, named, postConstruct } from 'inversify'
import {
  IdentityProviderServiceSymbol,
  IIdentityProviderService,
  AuthenticationError,
  PrismaClientSymbol,
} from '@flowda-projects/flowda-types'
import * as db from '@prisma/client-flowda'
import * as bcrypt from 'bcrypt'
import { JwtService } from '../jwt/jwt.service'
import { IConfigService, IMailService } from '../../infra/index'
import { Tenant } from '@prisma/client-flowda'
import {
  AuthenticationService,
  TExcludedIdentity,
  excludedIdentity,
} from '../authentication/authentication.service'
import { TenantEmailSignupDto } from './dto/tenantEmailSignup.dto'
import { tenantPreSignupResDto } from './dto/tenantPreSignupRes.dto'
import { tenantPreSignupDto } from './dto/tenantPreSignup.dto'
@injectable()
export class TenantAuthService extends AuthenticationService<Tenant> {
  constructor(
    @inject(IdentityProviderServiceSymbol)
    @named('tenant')
    protected identityProvider: IIdentityProviderService<Tenant>,
    @inject(JwtService) protected jwt: JwtService,
    @inject(IConfigService) protected config: IConfigService,
    @inject(IMailService) protected mailService: IMailService,
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
  ) {
    super(identityProvider, jwt, config, mailService)
  }

  @postConstruct()
  postConstruct() {
    this.setOptions({
      access_token_secret: this.config.getEnv('tenant_access_token_secret'),
      refresh_token_secret: this.config.getEnv('tenant_refresh_token_secret'),
      access_token_expire: this.config.getEnv('tenant_access_token_expire'),
      refresh_token_expire: this.config.getEnv('tenant_refresh_token_expire'),
    })
  }

  /**
   * 预注册
   * sdk.register
   */
  async preSignup(dto: tenantPreSignupDto): Promise<tenantPreSignupResDto> {
    // 一个邮箱只能注册一个
    const user = await this.identityProvider.find(dto)
    if (user) {
      throw new AuthenticationError.EmailAlreadyExists()
    }
    const randomCode = this.generateRandomUppercaseLetter()

    await this.prisma.tenantPreSignup.create({
      data: {
        email: dto.email,
        verifyCode: randomCode,
      },
    })

    await this.mailService.legacySendEmail(dto.email, 'Tenant register verify code - ', randomCode)

    return {
      verifyCode: randomCode,
    }
  }

  async signup(
    dto: Omit<TenantEmailSignupDto, 'verifyCode'>,
    extraFields: Record<string, any> = {},
  ): Promise<TExcludedIdentity<Tenant>> {
    const user = await this.identityProvider.find({
      email: dto.email,
    })
    if (user) {
      throw new AuthenticationError.AccountNameAlreadyExists()
    }

    let hashedPassword: string | null = null
    if (dto.password) {
      // todo: 先不做验证这一步，邮箱需要，但是微信/手机号不需要
      hashedPassword = await bcrypt.hash(dto.password, 10)
    }

    // 租户的 name 不需要强制校验
    if (!dto.name) {
      dto.name = dto.email
    }

    // const { password, ...rest } = dto // 删除 password
    const a = {
      ...dto, // 所有属性（除 password），identity 和非 identity 属性透传，这样子类可以按需保存
      hashedPassword: hashedPassword,
      hashedRefreshToken: null,
      recoveryCode: null,
      recoveryToken: null,
      ...extraFields,
    }
    const newUser = await this.identityProvider.create(a)

    return excludedIdentity<Tenant>(newUser)
  }

  async verifyAndSignup(dto: TenantEmailSignupDto): Promise<TExcludedIdentity<Tenant>> {
    const rt = await this.prisma.tenantPreSignup.findFirst({
      where: {
        AND: [{ email: dto.email }, { verifyCode: dto.verifyCode }],
      },
    })
    if (!rt) {
      throw new AuthenticationError.WrongVerifyCode()
    }

    const newUser = await this.signup({
      name: dto.name,
      password: dto.password,
      email: dto.email,
    })

    await this.prisma.tenantPreSignup.delete({
      where: {
        id: rt.id,
      },
    })

    return newUser
  }
}
