import { inject, injectable } from 'inversify'
import { LoggerService } from '@nestjs/common'
import { PrismaClientSymbol } from '@flowda-projects/flowda-types'
import { PrismaClient, Product } from '@prisma/client-flowda'

@injectable()
export class ProductQuery {
  private readonly logger: LoggerService

  constructor(
    @inject(PrismaClientSymbol) private prisma: PrismaClient,
    @inject('Factory<Logger>')
    loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(ProductQuery.name)
  }

  findAll(appId: string): Promise<Product[]> {
    return this.prisma.product.findMany({
      where: {
        appId,
      },
    })
  }

  async findAllByAppName(appName: string): Promise<Product[]> {
    const app = await this.prisma.app.findFirstOrThrow({
      where: {
        name: appName,
      },
    })
    return this.prisma.product.findMany({
      where: {
        appId: app.id,
      },
    })
  }

  async findById(id: string): Promise<Product> {
    return this.prisma.product.findUniqueOrThrow({
      where: { id },
    })
  }
}
