import { SignupDto } from './dto/signup.dto'
import { inject, injectable } from 'inversify'
import { PrismaClientSymbol } from '@flowda-projects/flowda-types'
import { LoggerService } from '@nestjs/common'
import { SuperAdminAuthenticationService } from './superAdminAuthentication.service'
import * as db from '@prisma/client-flowda'
import { SupperAdminSignupDto } from './dto/supperAdminSignup.dto'

@injectable()
export class SuperAdminAuthenticationTx {
  private readonly logger: LoggerService

  constructor(
    @inject(SuperAdminAuthenticationService) private service: SuperAdminAuthenticationService,
    @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(SuperAdminAuthenticationTx.name)
  }

  async signup(dto: SupperAdminSignupDto): Promise<any> {
    return this.prisma.$transaction(tx => this.service.signup(dto, { tx }))
  }
}
