import { ContainerModule, interfaces } from 'inversify'
import { PrismaClient } from '@prisma/client-flowda'
import { PrismaClientSymbol } from '@flowda-projects/flowda-types'

const prisma = new PrismaClient({
  // log: ['query', 'info', 'warn', 'error'],
})

export const prismaClientFlowdaModule = new ContainerModule((bind: interfaces.Bind) => {
  bind<PrismaClient>(PrismaClientSymbol).toConstantValue(prisma)
})
