import { ContainerModule, interfaces } from 'inversify'
import {
  CustomZodSchemaSymbol,
  IdentityProviderServiceSymbol,
  IIdentityProviderService,
  PrismaZodSchemaSymbol,
  ServiceSymbol,
  zt,
} from '@flowda-projects/flowda-types'
import { WxPayService } from './wx-pay/wxPay.service'
import { bindService } from '@flowda-projects/flowda-shared'
import { JwtService } from './jwt/jwt.service'
import { TenantIdentityProviderService } from './identity-provider/tenantIdentityProvider.service'
import { App, Customer, Tenant } from '@prisma/client-flowda'
import { AppIdentityProviderService } from './identity-provider/appIdentityProvider.service'
import { CustomerIdentityProviderService } from './identity-provider/customerIdentityProvider.service'
import { TenantAuthService } from './tenant/tenantAuth.service'
import { AppAuthService } from './app/appAuth.service'
import { CustomerAuthService } from './customer-auth/customerAuth.service'
import { OrderService } from './order/order.service'
import { CustomerService } from './customer/customer.service'
import { ProductService } from './product/product.service'
import { WxLoginService } from './wx-login/wxLogin.service'
import { WxFwhLoginService } from './wx-login/wxFwhLogin.service'
import { AppService } from './app/app.service'
import { ProductQuery } from './product/product.query'
import { ProductTx } from './product/product.tx'
import { OrderTx } from './order/order.tx'
import { OrderQuery } from './order/order.query'
import { CustomerTx } from './customer/customer.tx'
import { SuperAdminAuthenticationTx } from './authentication/superAdminAuthentication.tx'
import { SuperAdminAuthenticationQuery } from './authentication/superAdminAuthentication.query'
import { SuperAdminAuthenticationService } from './authentication/superAdminAuthentication.service'
import { TenantQuery } from './tenant/tenant.query'
import * as schema from './schema/schema'

export const flowdaServicesModule = new ContainerModule((bind: interfaces.Bind) => {
  // const schema = generateSchema()
  bind(PrismaZodSchemaSymbol).toConstantValue(zt)
  bind(CustomZodSchemaSymbol).toConstantValue(schema)

  // bind(TenantService).toSelf().inRequestScope()
  bind(WxPayService).toSelf().inSingletonScope()
  bind(JwtService).toSelf().inSingletonScope()
  bind(WxLoginService).toSelf().inSingletonScope()
  bind(WxFwhLoginService).toSelf().inSingletonScope()

  bindService(bind, ServiceSymbol, TenantAuthService)
  bindService(bind, ServiceSymbol, AppService)

  // sdk
  bindService(bind, ServiceSymbol, AppAuthService)
  bindService(bind, ServiceSymbol, CustomerAuthService)
  bindService(bind, ServiceSymbol, OrderService)
  bindService(bind, ServiceSymbol, OrderTx)
  bindService(bind, ServiceSymbol, OrderQuery)
  bindService(bind, ServiceSymbol, CustomerService)
  bindService(bind, ServiceSymbol, CustomerTx)
  bindService(bind, ServiceSymbol, ProductService)
  bindService(bind, ServiceSymbol, ProductQuery)
  bindService(bind, ServiceSymbol, ProductTx)

  bind(SuperAdminAuthenticationService).toSelf().inSingletonScope()
  bindService(bind, ServiceSymbol, SuperAdminAuthenticationTx)
  bindService(bind, ServiceSymbol, SuperAdminAuthenticationQuery)

  bind(TenantQuery).toSelf().inSingletonScope()

  // identity provider
  bind<IIdentityProviderService<Tenant>>(IdentityProviderServiceSymbol)
    .to(TenantIdentityProviderService)
    .whenTargetNamed('tenant')
  bind<IIdentityProviderService<App>>(IdentityProviderServiceSymbol)
    .to(AppIdentityProviderService)
    .whenTargetNamed('app')
  bind<IIdentityProviderService<Customer>>(IdentityProviderServiceSymbol)
    .to(CustomerIdentityProviderService)
    .whenTargetNamed('customer')
})
