import { inject, injectable } from 'inversify'
import { SdkCreateOrderDto } from './dto/sdkCreateOrder.dto'
import { LoggerService } from '@nestjs/common'
import { EPlan, IIdentity, OrderError, PrismaTx } from '@flowda-projects/flowda-types'
import * as db from '@prisma/client-flowda'
import { TWxPayQuery, WxPayService } from '../wx-pay/wxPay.service'
import { SdkCreateOrderResDto } from './dto/sdkCreateOrderRes.dto'
import { excludedIdentity, TExcludedIdentity } from '../authentication/authentication.service'
import { SdkCreateQuickOrderDto } from './dto/sdkCreateQuickOrder.dto'
import { ProductService } from '../product/product.service'
import { CustomerService } from '../customer/customer.service'
import { ProductQuery } from '../product/product.query'
import { SdkCreateOrderInJSAPIDto } from './dto/sdkCreateOrderInJSAPI.dto'
import { SdkCreateOrderInJSAPIResDto } from './dto/sdkCreateOrderInJSAPIRes.dto'
import { OrderQuery } from './order.query'

export const Serial_Min = 10001
export const Serial_Max = 99999

export type TQueryPayRes = {
  order: db.Order & {
    productSnapshots: db.ProductSnapshot[]
    customer: TExcludedIdentity<db.Customer>
  }
  payQueryRet: TWxPayQuery
}

@injectable()
export class OrderService {
  private readonly logger: LoggerService

  constructor(
    @inject(WxPayService) private wxPayService: WxPayService,
    @inject(ProductService) private productService: ProductService,
    @inject(ProductQuery) private productQuery: ProductQuery,
    @inject(OrderQuery) private orderQuery: OrderQuery,
    @inject(CustomerService) private customerService: CustomerService,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(OrderService.name)
  }

  async create(
    user: IIdentity,
    dto: SdkCreateOrderDto,
    { tx }: PrismaTx,
  ): Promise<SdkCreateOrderResDto> {
    const { product, productSnapshot, order } = await this.doCreate(user.id, dto.productId, { tx })
    const profile = await tx.profile.findUnique({ where: { customerId: user.id } })
    // 检查限购情况
    if (product.restricted) {
      const purchased = await this.orderQuery.queryOrderHistory(user.id, product.id)
      if (purchased?.length > 0) {
        throw new OrderError.PurchaseReactedRestrictedLimit()
      }
    }

    // 免费产品
    if (productSnapshot.snapshotPrice.toNumber() === 0) {
      const { updatedOrder, customer } = await this.processFreeOrder(
        order.id,
        user.id,
        profile,
        product,
        { tx },
      )
      return {
        order: updatedOrder,
        customer: excludedIdentity(customer),
        productSnapshot,
        codeUrl: '',
      }
    } else {
      // // 购买付费产品后，不要重复购买，这块逻辑先丑一点挡一下
      // if (profile && profile.plan === EPlan.VIP) {
      //   throw new OrderError.CannotPayVIPIfPaid()
      // }

      // 发起微信支付
      this.logger.log(`product price ${productSnapshot.snapshotPrice}, call wechat pay`)
      const wxRet: { status: number; code_url: string } =
        await this.wxPayService.transactionsNative(
          order.id,
          product.name,
          productSnapshot.snapshotPrice.toNumber(),
        )
      const { updatedOrder, customer } = await this.processPaidOrder(order.id, user, { tx })
      return {
        order: updatedOrder,
        customer: excludedIdentity(customer),
        productSnapshot,
        codeUrl: wxRet.code_url,
      }
    }
  }

  /**
   * 在 JSAPI 场景，即微信端创建支付订单
   */
  async createJSAPI(
    user: IIdentity,
    dto: SdkCreateOrderInJSAPIDto,
    { tx }: PrismaTx,
  ): Promise<SdkCreateOrderInJSAPIResDto> {
    const { product, productSnapshot, order } = await this.doCreate(user.id, dto.productId, { tx })
    const profile = await tx.profile.findUnique({ where: { customerId: user.id } })

    // 检查限购情况
    if (product.restricted) {
      const purchased = await this.orderQuery.queryOrderHistory(user.id, product.id)
      if (purchased?.length > 0) {
        throw new OrderError.PurchaseReactedRestrictedLimit()
      }
    }

    // 免费产品
    if (productSnapshot.snapshotPrice.toNumber() === 0) {
      const { updatedOrder, customer } = await this.processFreeOrder(
        order.id,
        user.id,
        profile,
        product,
        { tx },
      )
      return {
        order: updatedOrder,
        customer: excludedIdentity(customer),
        productSnapshot,
      }
    } else {
      // 购买付费产品后，不要重复购买，这块逻辑先丑一点挡一下
      // if (profile && profile.plan === EPlan.VIP) {
      //   throw new OrderError.CannotPayVIPIfPaid()
      // }

      // 发起微信支付
      this.logger.log(`product price ${productSnapshot.snapshotPrice}, call wechat jsapi pay`)
      const wxRet = await this.wxPayService.transactionsJSAPI(
        dto.openid,
        order.id,
        product.name,
        productSnapshot.snapshotPrice.toNumber(),
      )
      const { updatedOrder, customer } = await this.processPaidOrder(order.id, user, { tx })
      return {
        order: updatedOrder,
        customer: excludedIdentity(customer),
        productSnapshot,
        wxRet,
      }
    }
  }

  /*
     - 这个方法就非常复杂，还涉及到了 private methods，就很难 test
     - 但是如果内聚了，还真容易出现这种情况
     - 那还是通过 jest 的 spyOn，拿到 instance 后 mock 相应的 method 吧
     */
  async doCreate(
    customerId: string,
    productId: string,
    { tx }: PrismaTx,
  ): Promise<{
    order: db.Order
    productSnapshot: db.ProductSnapshot
    product: db.Product
  }> {
    this.logger.log(`${customerId} creating order of product: ${productId} `)
    const order = await this.createOrder(customerId, { tx })
    const { product, snapshot } = await this.productService.createProductSnapshot(
      productId,
      order.id,
      { tx },
    )
    return {
      order,
      product: product,
      productSnapshot: snapshot,
    }
  }

  public async createOrder(customerId: string, { tx }: PrismaTx) {
    const last = await tx.order.findFirst({
      orderBy: [
        {
          createdAt: 'desc',
        },
      ],
    })
    let serial
    if (last == null || last.serial >= Serial_Max) {
      serial = Serial_Min
    } else {
      serial = last.serial + 1
    }
    const customer = await tx.customer.findUniqueOrThrow({ where: { id: customerId } })
    const order = await tx.order.create({
      data: {
        tenantId: customer.tenantId,
        appId: customer.appId,
        customerId,
        status: db.OrderStatus.INITIALIZED,
        serial: serial,
      },
    })
    this.logger.log(`order created: ${order.id}`)
    return {
      ...order,
      serial, // 方便测试
    }
  }

  async createQuick(
    appId: string,
    dto: SdkCreateQuickOrderDto,
    { tx }: PrismaTx,
  ): Promise<SdkCreateOrderResDto> {
    const anonymous = await this.customerService.createAnonymous(
      dto.anonymousCustomerToken,
      appId,
      { tx },
    )
    return this.create(anonymous, dto, { tx })
  }

  async queryPayQuick(anonymousCustomerToken: string, orderId: string, { tx }: PrismaTx) {
    const findRet = await tx.customer.findFirstOrThrow({
      where: { name: anonymousCustomerToken },
    })
    // todo: 怎么出现了两次 queryPay???
    const ret = await this.queryPay(findRet.id, orderId, { tx })
    const openid = ret.payQueryRet.payer.openid
    const updateRet = await this.customerService.updateAnonymousToPayOpenId(
      anonymousCustomerToken,
      openid,
      { tx },
    )
    return this.queryPay(updateRet.id, orderId, { tx })
  }

  async queryPay(
    userId: string,
    orderId: string,
    { tx }: PrismaTx,
  ): Promise<
    TQueryPayRes & {
      customer: TExcludedIdentity<db.Customer> & { profile?: db.Profile }
    }
  > {
    const { order, payQueryRet } = await this.doQueryPay(orderId, { tx })
    if (order.customerId !== userId) {
      throw new OrderError.OrderCustomerIdNotMatch() // todo: 这块应该属于 authorization
    }
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore todo 删除 order 关联的 customer 信息，主要是调用的是 legacyQuery，等待重构
    delete order.customer
    // todo: 后续重构成 productSnapshot 和 order 1-1，暂时先取第一个
    const productId = order.productSnapshots[0].productId
    const product = await this.productQuery.findById(productId)
    const customer = await this.customerService.updatePaidProfile(userId, product, { tx })
    return {
      order,
      payQueryRet,
      customer: {
        ...excludedIdentity(customer),
        profile: customer.profile === null ? undefined : customer.profile,
      },
    }
  }

  // todo: 将 domain 层的几个函数上移到 service 层，这样便于依赖注入
  public async doQueryPay(orderId: string, { tx }: PrismaTx): Promise<TQueryPayRes> {
    this.logger.log(`querying orderId ${orderId} ...`)
    const order = await tx.order.findFirstOrThrow({
      where: { id: orderId },
      include: {
        customer: true,
        productSnapshots: true,
      },
    })
    this.logger.log(`order found ${order.id} query request to wx`)
    const payQueryRet = await this.wxPayService.query(orderId)
    this.logger.log(`order query response from wx ${payQueryRet?.trade_state}`)
    if (payQueryRet.status !== 200 || payQueryRet.trade_state !== 'SUCCESS') {
      throw new OrderError.PayQueryStatusNotOk(payQueryRet)
    }
    this.logger.log(`order query success`)

    // 创建支付关联订单
    await tx.pay.upsert({
      where: {
        orderId: orderId,
      },
      create: {
        tenantId: order.tenantId,
        status: db.PayStatus.PAIED,
        orderId: orderId,
        transactionId: payQueryRet.transaction_id,
      },
      update: {
        status: db.PayStatus.PAIED,
        transactionId: payQueryRet.transaction_id,
      },
    })
    return {
      order: {
        ...order,
        customer: excludedIdentity(order.customer),
      },
      payQueryRet,
    }
  }

  private async processPaidOrder(orderId: string, user: IIdentity, { tx }: PrismaTx) {
    const updatedOrder = await tx.order.update({
      where: { id: orderId },
      data: { status: db.OrderStatus.PAY_ASSOCIATED },
    })
    this.logger.log(`order ${orderId} update to status ${db.OrderStatus.PAY_ASSOCIATED}`)
    const customer = await tx.customer.findFirstOrThrow({
      where: {
        id: user.id,
      },
    })
    return { updatedOrder, customer }
  }

  private async processFreeOrder(
    orderId: string,
    userId: string,
    profile: db.Profile | null,
    product: db.Product,
    { tx }: PrismaTx,
  ) {
    // // 购买免费产品之后，可以购买付费产品，但是购买了付费产品，不能再购买免费产品，
    // // 这块逻辑先丑一点挡一下
    // if (profile && profile.plan === EPlan.VIP) {
    //   throw new OrderError.CannotPayFreeIfPaid()
    // }

    this.logger.log(`product is free, create order status ${db.OrderStatus.FREE_DEAL}`)
    const updatedOrder = await tx.order.update({
      where: { id: orderId },
      data: { status: db.OrderStatus.FREE_DEAL },
    })
    const customer = await this.customerService.updateFreeProfile(userId, product, { tx })
    return { updatedOrder, customer }
  }
}
