// https://github.com/omar-dulaimi/prisma-trpc-generator
// https://github.com/macstr1k3r/trpc-nestjs-adapter

import { z } from 'zod'
import {
  AppWithRelationsSchema,
  CustomerWithRelationsSchema,
  OrderWithRelationsSchema,
  ProductSnapshotWithRelationsSchema,
  ProductWithRelationsSchema,
  ProfileWithRelationsSchema,
  WeixinProfileWithRelationsSchema,
} from '@flowda-projects/flowda-types'
import { meta } from '@flowda-projects/flowda-engine'

// __meta 得增加 extend，去拿 openapi 定义
export const ProductResourceSchema = ProductWithRelationsSchema.omit({
  fileSize: true,
  storeDuration: true,
  // todo: buildYup 兼容性有点差
  // price: true, // oneOf 这种挂
  extendedDescriptionData: true,
  app: true,
  isDeleted: true,
}).extend({
  // motor-admin schema
  __meta: meta({
    extends: 'ProductSchema',
  }),
})

export const ProductSnapshotResourceSchema = ProductSnapshotWithRelationsSchema.omit({}).extend({
  // motor-admin schema
  __meta: meta({
    extends: 'ProductSnapshotSchema',
  }),
})

export const AppResourceSchema = AppWithRelationsSchema.pick({
  id: true,
  name: true,
  displayName: true,
  description: true,
  products: true,
  orders: true,
  customers: true,
}).extend({
  appToken: z.string().nullable().openapi({ title: '应用 Token', access_type: 'read_only' }),
  __meta: meta({
    extends: 'AppSchema',
  }),
})

export const CustomerResourceSchema = CustomerWithRelationsSchema.omit({
  legacyProfile: true,
  hashedPassword: true,
  hashedRefreshToken: true,
  recoveryCode: true,
  recoveryToken: true,
  app: true,
  isDeleted: true,
}).extend({
  __meta: meta({
    extends: 'CustomerSchema',
  }),
})

export const WeixinProfileResourceSchema = WeixinProfileWithRelationsSchema.omit({
  customerId: true,
  customer: true,
}).extend({
  __meta: meta({
    extends: 'WeixinProfileSchema',
  }),
})

export const ProfileResourceSchema = ProfileWithRelationsSchema.omit({
  customerId: true,
  customer: true,
}).extend({
  __meta: meta({
    extends: 'ProfileSchema',
  }),
})

export const OrderResourceSchema = OrderWithRelationsSchema.omit({
  customer: true,
  status: true,
  isDeleted: true,
  productSnapshots: true,
  pay: true,
  App: true,
}).extend({
  __meta: meta({
    extends: 'OrderSchema',
  }),
})
