import { inject, injectable } from 'inversify'
import {
  AuthenticationError,
  IIdentityProviderService,
  PrismaClientSymbol,
} from '@flowda-projects/flowda-types'
import { Customer, Prisma, PrismaClient } from '@prisma/client-flowda'

@injectable()
export class CustomerIdentityProviderService implements IIdentityProviderService<Customer> {
  constructor(@inject(PrismaClientSymbol) private prisma: PrismaClient) {}

  async create(dto: Omit<Customer, 'id'>): Promise<Customer> {
    /*if (!dto.hashedPassword) {
      throw new SdkError.NoPassword()
    }
    if (!dto.email) {
      throw new AuthenticationError.NoEmail()
    }*/
    const user = await this.prisma.customer.create({
      data: {
        tenantId: dto.tenantId,
        appId: dto.appId,
        name: dto.name,
        email: dto.email,
        hashedPassword: dto.hashedPassword,
        hashedRefreshToken: null,
      },
    })
    return user
  }

  async find(query: Partial<Customer>): Promise<Prisma.CustomerGetPayload<{
    include: { profile: true; weixinProfile: true }
  }> | null> {
    const appIdQuery: any = { appId: query.appId }
    const or: Record<string, string>[] = []

    if (query.id) {
      or.push({ id: query.id })
      delete appIdQuery.appId
    } else {
      if (!query.appId) {
        throw new AuthenticationError.InvalidAppId()
      }
    }
    if (query.name) {
      or.push({ name: query.name })
    }
    if (query.email) {
      or.push({ email: query.email })
    }
    if (query.recoveryCode) {
      or.push({ recoveryCode: query.recoveryCode })
    }
    const customer = await this.prisma.customer.findFirst({
      where: {
        AND: [appIdQuery, { OR: or }],
      },
      include: {
        profile: true,
        weixinProfile: true,
      },
    })
    return customer

    /*if (customer) {
      const profile = await this.prisma.profile.findUnique({
        where: {
          customerId: customer.id,
        },
      })
      return {
        ...customer,
        // todo: 增加 e2e test
        profile: profile === null ? undefined : profile, // 必须是 undefined，否则 update 会报错
      }
    } else {
      return null
    }*/
  }

  update(user: Prisma.CustomerUpdateInput): Promise<Customer> {
    const { profile, weixinProfile, ...customer } = user
    return this.prisma.customer.update({
      where: { id: user.id as string },
      data: customer,
    })
  }
}
