import { WechatpayNodeV3, WechatpayNodeV3FactorySymbol } from '../../legacy-libs'
import { LoggerService } from '@nestjs/common'
import { inject, injectable } from 'inversify'
import { getTimeExpire } from '../../utils/dayjs'
import { OrderError } from '@flowda-projects/flowda-types'

export type TWxPayQuery = {
  status: number
  trade_state: string
  transaction_id: string
  payer: {
    openid: string
  }
}

@injectable()
export class WxPayService {
  private readonly logger: LoggerService

  constructor(
    @inject(WechatpayNodeV3FactorySymbol)
    private wechatPayNodeV3Factory: () => typeof WechatpayNodeV3,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(WxPayService.name)
  }

  /*
  {
  "status": 200,
  "appId": "xx",
  "timeStamp": "1682132086",
  "nonceStr": "xx",
  "package": "prepay_id=xx",
  "signType": "RSA",
  "paySign": "xx"
}
   */
  async transactionsJSAPI(
    openid: string,
    orderId: string,
    desc: string,
    total: number,
  ): Promise<{
    prepay_id: string
  }> {
    const timeExpire = getTimeExpire(5)
    const params = {
      description: desc, ///商品描述
      out_trade_no: orderId, ///户系统内部订单号，只能是数字、大小写字母_-*且在同一个商户号下唯一
      time_expire:
        timeExpire /*订单失效时间，遵循rfc3339标准格式，格式为yyyy-MM-DDTHH:mm:ss+TIMEZONE，yyyy-MM-DD表示年月日，T出现在字符串中，表示time元素的开头，HH:mm:ss表示时分秒，TIMEZONE表示时区（+08:00表示东八区时间，领先UTC8小时，即北京时间）。例如：2015-05-20T13:29:35+08:00表示，北京时间2015年5月20日 13点29分35秒。 */,
      attach: '附加数据', ///附加数据，在查询API和支付通知中原样返回，可作为自定义参数使用，实际情况下只有支付完成状态才会返回该字段。
      notify_url: 'https://www.weixin.qq.com/wxpay/pay.php', // todo /* 异步接收微信支付结果通知的回调地址，通知url必须为外网可访问的url，不能携带参数。 公网域名必须为https，如果是走专线接入，使用专线NAT IP或者私有回调域名可使用http */
      support_fapiao: false, ///传入true时，支付成功消息和支付详情页将出现开票入口。需要在微信支付商户平台或微信公众平台开通电子发票功能，传此字段才可生效。
      amount: {
        total: total * 100, ///订单总金额，单位为“分”
        currency: 'CNY', /// CNY：人民币，境内商户号仅支持人民币。
      },
      payer: {
        openid: openid, ///用户在直连商户appid下的唯一标识，不可混用
      },
      settle_info: {
        profit_sharing: false, ///是否指定分账
      },
    }
    const wxRet = await this.wechatPayNodeV3Factory().transactions_jsapi(params)
    this.logger.log(`wechat transactions_jsapi resp ${JSON.stringify(wxRet)}`)
    if (wxRet.status !== 200) {
      throw new OrderError.TransactionsNativeStatusNotOk()
    }
    return wxRet
  }

  async transactionsNative(
    orderId: string,
    desc: string,
    total: number,
  ): Promise<{
    status: number
    code_url: string
  }> {
    const timeExpire = getTimeExpire(5)
    const params = {
      description: desc, ///商品描述
      out_trade_no: orderId, ///户系统内部订单号，只能是数字、大小写字母_-*且在同一个商户号下唯一
      // '2022-11-11T23:59:59+08:00'
      time_expire: timeExpire,
      /*订单失效时间，遵循rfc3339标准格式，格式为yyyy-MM-DDTHH:mm:ss+TIMEZONE，yyyy-MM-DD表示年月日，T出现在字符串中，表示time元素的开头，HH:mm:ss表示时分秒，TIMEZONE表示时区（+08:00表示东八区时间，领先UTC8小时，即北京时间）。例如：2015-05-20T13:29:35+08:00表示，北京时间2015年5月20日 13点29分35秒。 */
      attach: '附加数据', ///附加数据，在查询API和支付通知中原样返回，可作为自定义参数使用，实际情况下只有支付完成状态才会返回该字段。
      notify_url: 'https://www.weixin.qq.com/wxpay/pay.php', // todo /* 异步接收微信支付结果通知的回调地址，通知url必须为外网可访问的url，不能携带参数。 公网域名必须为https，如果是走专线接入，使用专线NAT IP或者私有回调域名可使用http */
      support_fapiao: false, ///传入true时，支付成功消息和支付详情页将出现开票入口。需要在微信支付商户平台或微信公众平台开通电子发票功能，传此字段才可生效。
      amount: {
        total: total * 100, ///订单总金额，单位为“分”
        currency: 'CNY', /// CNY：人民币，境内商户号仅支持人民币。
      },
      settle_info: {
        profit_sharing: false, ///是否指定分账
      },
    }
    const wxRet = await this.wechatPayNodeV3Factory().transactions_native(params)
    this.logger.log(`wechat transactions_native resp ${JSON.stringify(wxRet)}`)
    if (wxRet.status !== 200) {
      throw new OrderError.TransactionsNativeStatusNotOk()
    }
    return wxRet as {
      status: number
      code_url: string
    }
  }

  /*
  {
    "status": 200,
    "amount": {
        "currency": "CNY",
        "payer_currency": "CNY",
        "payer_total": 1,
        "total": 1
    },
    "appid": "wx6ecc94d7d5133fde",
    "attach": "附加数据",
    "bank_type": "OTHERS",
    "mchid": "1634638724",
    "out_trade_no": "claz2v5la0000tzp2ivnp5gpm",
    "payer": {
        "openid": "oQBzz5GM-9aCngjG3eNpqJIlzJj4"
    },
    "promotion_detail": [],
    "success_time": "2022-11-27T16:07:27+08:00",
    "trade_state": "SUCCESS",
    "trade_state_desc": "支付成功",
    "trade_type": "NATIVE",
    "transaction_id": "4200001645202211278892941061"
}
   */
  async query(outTradeNo: string): Promise<TWxPayQuery> {
    this.logger.log(`wx pay query with tradeNo ${outTradeNo}`)
    const ret = await this.wechatPayNodeV3Factory().query({ out_trade_no: outTradeNo })
    this.logger.log(`wx pay query resp: ${JSON.stringify(ret)}`)
    return ret as TWxPayQuery
  }
}
