import { ContainerModule, interfaces } from 'inversify'
import { IMailService } from './interfaces/mail/mail.service'
import { MailService } from './mail/mail.service'
import { ConfigService } from './config/config.service'
import { COSSymbol } from '@flowda-projects/flowda-types'
import { IConfigService } from './interfaces/config/config.service'
import {
  COS,
  WechatOAuth,
  WechatOAuthFactorySymbol,
  WechatOAuthSymbol,
  WechatpayNodeV3,
  WechatpayNodeV3FactorySymbol,
  WechatpayNodeV3Symbol,
} from '../legacy-libs'

export const flowdaInfraModule = new ContainerModule((bind: interfaces.Bind) => {
  // 一些切面依赖暂时先放在这，应该放在基础设施层的，不过不强调过分学术的分层
  // 现在分出 domain 层的目标主要是为了解决循环依赖
  bind<IMailService>(IMailService).to(MailService).inSingletonScope()
  bind<IConfigService>(IConfigService).to(ConfigService).inSingletonScope()

  bind<typeof COS>(COSSymbol)
    .toDynamicValue((context: interfaces.Context) => {
      const config = context.container.get<IConfigService>(IConfigService)
      return new COS({
        SecretId: config.getEnv('TENCENT_SEC_ID'),
        SecretKey: config.getEnv('TENCENT_SEC_KEY'),
      })
    })
    .inSingletonScope()

  bind<typeof WechatpayNodeV3>(WechatpayNodeV3Symbol)
    .toDynamicValue((context: interfaces.Context) => {
      const config = context.container.get<IConfigService>(IConfigService)
      return new WechatpayNodeV3({
        appid: config.getEnv('appid'),
        mchid: config.getEnv('mchid'),
        publicKey: Buffer.from(config.getEnv('apiclient_cert.pem'), 'utf-8'),
        privateKey: Buffer.from(config.getEnv('apiclient_key.pem'), 'utf-8'),
      })
    })
    .inSingletonScope()

  bind<interfaces.Factory<typeof WechatpayNodeV3>>(WechatpayNodeV3FactorySymbol).toFactory<
    typeof WechatpayNodeV3,
    []
  >(context => {
    return () => context.container.get(WechatpayNodeV3Symbol)
  })

  bind<typeof WechatOAuth>(WechatOAuthSymbol)
    .toDynamicValue((context: interfaces.Context) => {
      const config = context.container.get<IConfigService>(IConfigService)
      return new WechatOAuth(
        config.getEnv('freecharger_pc_appid'),
        config.getEnv('freecharger_pc_appSecret'),
      )
    })
    .inSingletonScope()

  bind<interfaces.Factory<typeof WechatOAuth>>(WechatOAuthFactorySymbol).toFactory<
    typeof WechatOAuth,
    []
  >(context => {
    return () => context.container.get(WechatOAuthSymbol)
  })
})
