import { inject, injectable } from 'inversify'
import { PrismaClientSymbol } from '@flowda-projects/flowda-types'
import { PrismaClient, Tenant } from '@prisma/client-flowda'
import { IIdentityProviderService } from '@flowda-projects/flowda-types'

@injectable()
export class TenantIdentityProviderService implements IIdentityProviderService<Tenant> {
  constructor(@inject(PrismaClientSymbol) private prisma: PrismaClient) {}

  async create(dto: Omit<Tenant, 'id'>): Promise<Tenant> {
    const user = await this.prisma.tenant.create({
      data: {
        name: dto.name,
        email: dto.email,
        hashedPassword: dto.hashedPassword,
        hashedRefreshToken: null,
      },
    })
    return user
  }

  find(query: Partial<Tenant>): Promise<Tenant | null> {
    const or: any[] = []
    if (query.id) {
      or.push({ id: query.id })
    }
    if (query.name) {
      or.push({ name: query.name })
    } else if (query.email) {
      or.push({ email: query.email })
    }
    return this.prisma.tenant.findFirst({
      where: {
        OR: or,
      },
    })
  }

  update(user: Tenant): Promise<Tenant> {
    return this.prisma.tenant.update({
      where: { id: user.id },
      data: user,
    })
  }
}
