import { inject, injectable } from 'inversify'
import { LoggerService } from '@nestjs/common'
import { PrismaTx } from '@flowda-projects/flowda-types'
import * as db from '@prisma/client-flowda'
import { SdkProductCreateManyItemDto } from './dto/sdkProductCreateManyItem.dto'

@injectable()
export class ProductService {
  private readonly logger: LoggerService

  constructor(
    @inject('Factory<Logger>')
    loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(ProductService.name)
  }

  async createManyProducts(
    appId: string,
    list: SdkProductCreateManyItemDto[],
    { tx }: PrismaTx,
  ): Promise<db.Product[]> {
    // todo: 根据 appId -> tenantId
    const app = await tx.app.findUniqueOrThrow({ where: { id: appId } })
    const data = list.map(item => ({
      ...item,
      tenantId: app.tenantId,
      appId: appId,
      productType: item.productType as db.ProductType,
      restricted: item.restricted || 0,
      // 以下是为了处理 prisma null
      amount: item.amount === undefined ? null : item.amount,
      plan: item.plan === undefined ? null : item.plan,
      extendedDescriptionData: item.extendedDescriptionData,
      fileSize: item.fileSize === undefined ? null : item.fileSize,
      storeDuration: item.storeDuration === undefined ? null : item.storeDuration,
      hasAds: item.hasAds === undefined ? null : item.hasAds,
      tecSupport: item.tecSupport === undefined ? null : item.tecSupport,
      validityPeriod: null,
    }))
    await tx.product.createMany({ data })
    return tx.product.findMany({
      where: {
        appId: appId,
      },
    })
  }

  private mapToProduct(appId: string, product: SdkProductCreateManyItemDto) {
    const data = {
      appId: appId,
      ...product,
      productType: product.productType as db.ProductType,
      // 以下是为了处理 prisma null
      amount: product.amount === undefined ? null : product.amount,
      plan: product.plan === undefined ? null : product.plan,
      fileSize: product.fileSize === undefined ? null : product.fileSize,
      storeDuration: product.storeDuration === undefined ? null : product.storeDuration,
      hasAds: product.hasAds === undefined ? null : product.hasAds,
      tecSupport: product.tecSupport === undefined ? null : product.tecSupport,
    }
    return data
  }

  async createProductSnapshot(
    productId: string,
    orderId: string,
    { tx }: PrismaTx,
  ): Promise<{
    product: db.Product
    snapshot: db.ProductSnapshot
  }> {
    const p = await tx.product.findFirstOrThrow({
      where: {
        id: productId,
      },
    })

    const snapshot = {
      snapshotPrice: p.price,
      orderId: orderId,
      productId: p.id,
      tenantId: p.tenantId,
    }

    const snapshotRet = await tx.productSnapshot.create({ data: snapshot })
    return {
      product: p,
      snapshot: snapshotRet,
    }
  }
}
