import { TTemplateName } from '../../mail/templates'

export const IMailService = Symbol.for('IMailService')
export * from '../../mail/templates'

export type TSendEmailOptions = {
  email: string
  templateName: TTemplateName
  params: any
}
export interface IMailService {
  sendLicense(email: string, license: string): Promise<void>
  sendEmail(options: TSendEmailOptions): Promise<void>
  legacySendEmail(email: string, subject: string, content: string): Promise<void>
}

export interface IMailOptions {
  /** The e-mail address of the sender. All e-mail addresses can be plain 'sender@server.com' or formatted 'Sender Name <sender@server.com>' */
  from?: string | undefined
  /** Comma separated list or an array of recipients e-mail addresses that will appear on the To: field */
  to?: string | Array<string> | undefined
  /** Comma separated list or an array of recipients e-mail addresses that will appear on the Cc: field */
  cc?: string | Array<string> | undefined
  /** Comma separated list or an array of recipients e-mail addresses that will appear on the Bcc: field */
  bcc?: string | Array<string> | undefined
  /** Comma separated list or an array of e-mail addresses that will appear on the Reply-To: field */
  replyTo?: string | Array<string> | undefined
  /** The message-id this message is replying */
  inReplyTo?: string | undefined
  /** Message-id list (an array or space separated string) */
  references?: string | string[] | undefined
  /** The subject of the e-mail */
  subject?: string | undefined
  /** The plaintext version of the message */
  text?: string | undefined
  /** The HTML version of the message */
  html?: string | undefined
}
