import { injectable } from 'inversify'
import * as env from 'dotenv'
import { IConfigService } from '../interfaces/config/config.service'
import {
  CleanedEnvAccessors,
  cleanEnv,
  email,
  EnvError,
  EnvMissingError,
  num,
  port,
  str,
  testOnly,
} from 'envalid'
import { Logger } from '@nestjs/common'

env.config()

@injectable()
export class ConfigService implements IConfigService {
  private env: Readonly<any & CleanedEnvAccessors>
  private readonly logger = new Logger(ConfigService.name)

  constructor() {
    this.env = cleanEnv(
      process.env,
      {
        DATABASE_URL: str({ devDefault: testOnly('test') }),
        SECRET_FOR_LICENSE_TOKEN: str({ devDefault: testOnly('test') }),

        // secret 不设置默认值，强制配置
        // customer
        customer_access_token_secret: str({ devDefault: testOnly('test') }),
        customer_refresh_token_secret: str({ devDefault: testOnly('test') }),
        customer_access_token_expire: num({ default: 7 * 24 * 60 * 60 }),
        customer_refresh_token_expire: num({ default: 12 * 30 * 24 * 60 * 60 }),

        // app
        app_access_token_secret: str({ devDefault: testOnly('test') }),
        app_refresh_token_secret: str({ devDefault: testOnly('test') }),
        app_access_token_expire: num({ default: 7 * 24 * 60 * 60 }),
        app_refresh_token_expire: num({ default: 12 * 30 * 24 * 60 * 60 }),

        app_token_secret: str({ devDefault: testOnly('test') }),

        // tenant
        tenant_access_token_secret: str({ devDefault: testOnly('test') }),
        tenant_refresh_token_secret: str({ devDefault: testOnly('test') }),
        tenant_access_token_expire: num({ default: 10 * 60 }),
        tenant_refresh_token_expire: num({ default: 7 * 24 * 60 * 60 }),

        // 以下设置 default，是因为 e2e CI 上不需要强制配置
        // 但是其实生产环境需要设置下
        merchant_serial_no: str({ default: '' }),
        mchid: str({ default: '' }),
        'apiclient_key.pem': str({ default: '' }),
        'apiclient_cert.pem': str({ default: '' }),
        appid: str({ default: '' }),
        feishu_mail_account: email({ default: 'tset@test.com' }),
        feishu_mail_secret: str({ default: '' }),
        TENCENT_SEC_ID: str({ default: '' }),
        TENCENT_SEC_KEY: str({ default: '' }),
        mail_smtp: str({ default: '' }),
        mail_smtp_port: port({ default: 0 }),
        mail_account: str({ default: '' }),
        mail_secret: str({ default: '' }),
        freecharger_pc_appid: str({ default: '' }),
        freecharger_pc_appSecret: str({ default: '' }),
        freecharger_fuwuhao_appid: str({ default: '' }),
        freecharger_fuwuhao_secret: str({ default: '' }),
        fuwuhao_state_secret: str({ default: '' }),
        ZHI_CNT_PER_DAY: str({ default: 20 }),
        CHAT_SALT: str({ default: 'CHAT_SALT_STR' }),
        WORKER_LIST: str({ default: '' }),
        WECOM_HOOK_KEY: str({ default: 'f392b278-930a-4ccf-81ab-31128d668631' }),
      },
      {
        reporter: ({ errors, env }) => {
          for (const [envVar, err] of Object.entries(errors)) {
            if (err instanceof EnvError) {
              process.exit(1)
            } else if (err instanceof EnvMissingError) {
              this.logger.error(`missing ${envVar}`)
              process.exit(1)
            } else {
              process.exit(1)
            }
          }
        },
      },
    )
  }

  public getEnv(key: string): any {
    return this.env[key]
  }
}
