import readerTemplate from './reader'
import licenseTemplate from './license'

const templateMap = {
  COLLECT: readerTemplate,
  LICENSE: licenseTemplate,
}

export type TTemplateName = keyof typeof templateMap

export const render = (name: TTemplateName, params: any) => {
  const templateFn = templateMap[name]
  if (!templateFn) {
    throw new Error(`Email Template(${name}) not found`)
  }
  // eslint-disable-next-line @typescript-eslint/no-var-requires
  const render = templateFn.render
  const html = render(params)
  const subject = templateFn.renderSubject(params)
  return {
    html,
    subject,
  }
}
