import { inject, injectable } from 'inversify'
import { LoggerService } from '@nestjs/common'
import {
  EPlan,
  OrderError,
  PrismaTx,
  AuthenticationError,
  PrismaClientSymbol,
} from '@flowda-projects/flowda-types'
import { CustomerUpdateAmountDto } from './dto/customerUpdateAmount.dto'
import { PrismaClient } from '@prisma/client-flowda'
import * as db from '@prisma/client-flowda'
import { getTimeByDay } from '../../utils/dayjs'

@injectable()
export class CustomerService {
  private readonly logger: LoggerService

  constructor(
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
    @inject(PrismaClientSymbol) private prisma: PrismaClient,
  ) {
    this.logger = loggerFactory(CustomerService.name)
  }

  async createAnonymous(
    anonymousCustomerToken: string,
    appId: string,
    { tx }: PrismaTx,
  ): Promise<db.Customer> {
    const findRet = await tx.customer.findFirst({
      where: {
        appId,
        name: anonymousCustomerToken,
      },
    })
    if (findRet) {
      throw new OrderError.DuplicateAnonymousCustomerToken()
    }
    const app = await tx.app.findUniqueOrThrow({ where: { id: appId } })
    return tx.customer.create({
      data: {
        tenantId: app.tenantId,
        appId,
        name: anonymousCustomerToken,
      },
    })
  }

  async findAll(query?: any) {
    console.log('find query: ', query)
    let limit = 100
    if (query?.limit) {
      limit = query.limit
      delete query.limit
    }
    return this.prisma.customer.findMany({
      where: query,
      take: limit,
    })
  }

  async count(query?: any) {
    if (query?.limit) {
      delete query.limit
    }
    return this.prisma.customer.count({
      where: query,
    })
  }

  async findbyAppId(appId: string) {
    if (!appId) {
      throw new AuthenticationError.InvalidAppId()
    }
    return this.prisma.customer.findMany({
      where: { appId },
    })
  }

  /*
    todo: 修改，不要覆盖数据
    直接将匿名账户的 name 改成 openid
    未来合并账户的时候，再改成 unionid
   */
  async updateAnonymousToPayOpenId(
    anonymousCustomerToken: string,
    openid: string,
    { tx }: PrismaTx,
  ) {
    const findRet = await tx.customer.findFirstOrThrow({
      where: {
        name: anonymousCustomerToken,
      },
    })

    findRet.name = openid
    const updateRet = await tx.customer.update({
      where: { id: findRet.id },
      data: findRet,
      include: {
        profile: true,
        weixinProfile: true,
      },
    })
    return updateRet
  }

  /**
   * 更新用户额度
   * update amount
   */
  async amountUpdate(
    option: CustomerUpdateAmountDto & { userInfo: (db.Customer & { profile: any }) | null },
    { tx }: PrismaTx,
  ): Promise<any> {
    this.logger.log('updating amount: ', option)
    const profile = option.userInfo?.profile
    if (!profile) {
      throw new Error('User has no profile')
    }
    if (profile.expireAt) {
      if (new Date(profile.expireAt).valueOf() < Date.now()) {
        throw new Error('No valid amount')
      }
    }
    const cnt = option.count || 1
    const action = option.action || 'decrement'
    if (action === 'decrement') {
      if (profile.amount < cnt) {
        throw new Error('Reach amount limit')
      }
    } else {
      throw new Error('Action not allowed')
    }
    const resp = await this.updateProfileAmount(profile.id, action, cnt, { tx })
    return {
      ...option.userInfo,
      profile: resp,
    }
  }

  // 免费产品直接容易一份到 customer 相当于授权码
  // v1(free saver) 其实是用了 license Code
  // v2 通过登录实现，直接把 authorization code 挂载到 Customer 上
  async updateFreeProfile(
    userId: string,
    product: db.Product,
    { tx }: PrismaTx,
  ): Promise<db.Customer> {
    const prevProfile = await tx.profile.findUnique({
      where: {
        customerId: userId,
      },
    })
    // 订阅免费计划，不应该将已有计划清空（如果有的话）
    // TODO: Plan 的情况先不管
    const profile = {
      tenantId: product.tenantId,
      productType: product?.productType || db.ProductType.PLAN,
      plan: EPlan.Free,
      amount:
        product.amount == null ? prevProfile?.amount : product.amount + (prevProfile?.amount || 0),
      expireAt: null,
    }
    return tx.customer.update({
      where: {
        id: userId,
      },
      data: {
        profile: {
          upsert: {
            update: profile,
            create: profile,
          },
        },
      },
      include: {
        profile: true,
      },
    })
  }

  async updatePaidProfile(
    userId: string,
    product: db.Product,
    { tx }: PrismaTx,
  ): Promise<db.Customer & { profile: db.Profile | null }> {
    const prevProfile = await tx.profile.findUnique({
      where: {
        customerId: userId,
      },
    })
    const nextProfile = {
      tenantId: product.tenantId,
      productType: product.productType,
      plan: product.plan,
      amount:
        product.amount == null
          ? null
          : product.amount + (prevProfile && prevProfile.amount != null ? prevProfile.amount : 0),
      expireAt: product.validityPeriod != null ? getTimeByDay(product.validityPeriod) : null,
    }
    // 更新用户的 plan
    return tx.customer.update({
      where: {
        id: userId,
      },
      data: {
        profile: {
          upsert: {
            update: nextProfile,
            create: nextProfile,
          },
        },
      },
      include: {
        profile: true,
        weixinProfile: true,
      },
    })
  }

  private async updateProfileAmount(
    profileId: string,
    action: string,
    cnt: number,
    { tx }: PrismaTx,
  ) {
    // Cnt update
    const updated =
      action === 'decrement'
        ? {
            decrement: cnt || 1,
          }
        : {
            increment: cnt || 1,
          }
    const resp = await tx.profile.update({
      where: { id: profileId },
      data: {
        amount: updated,
      },
    })
    this.logger.log('update resp: ', resp)
    return resp
  }
}
